body {
  font-family: sans-serif;
}

.nav-bar {
  background: lightgrey;
}

.nav-item {
  position: relative;
  display: inline-block;
  padding: 10px;
  border: 1px solid black;
  text-align: center;
}

.tooltip {
  display: inline-block;
  white-space: nowrap;
  visibility: hidden;
  position: absolute;
  background: lightpink;
  left: 50%;
  transform: translateX(-50%);
  bottom: 100%;
  margin-bottom: 10px;
  padding: 4px;
  border-radius: 10px;
}

.tooltip:before {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  height: 10px;
  width: 10px;
  background: lightblue;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.nav-item:hover {
  background: lightgreen;
}

.nav-item:hover .tooltip {
  visibility: visible;
}
